import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import AppDemoContent from '../component/AppDemo/AppDemoContent'

class AppDemo extends Component {
  componentDidMount () {
    const {setDataLoaded, getDisasterSummary} = this.props

    setDataLoaded(false)
    getDisasterSummary()
  }

  render () {
    return (
      <main>
        <AppDemoContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    disasterSummary: state.disasterSummary,
    isDataLoaded: state.isDataLoaded
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDisasterSummary: () => dispatch(actionCreators.getDisasterSummary()),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(AppDemo)



