import * as actionTypes from './actionTypes'

const API_SERVICE = 'https://tpeoc.blob.core.windows.net/blobfs'

export const getGithubInfo = () => {
  return (dispatch) => {
    fetch('https://api.github.com/users/sky172839465')
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_USER_INFO,
          userInfo: data
        })
      })
  }
}

export const getDisasterSummary = () => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/GetDisasterSummary.json`)
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_DISASTER_SUMMARY,
          payload: {
            disasterSummary: data
          }
        })
      })
  }
}



