import * as actionTypes from './actionTypes'

export const setDataLoaded = (isDataLoaded) => {
  return {
    type: actionTypes.SET_DATA_LOADED,
    payload: {
      isDataLoaded
    }
  }
}

export const setCurrentRoute = (currentRoute) => {
  return {
    type: actionTypes.SET_CURRENT_ROUTE,
    payload: {currentRoute}
  }
}

export const toggleMobileDrawer = () => {
  return {
    type: actionTypes.TOGGLE_MOBILE_DRAWER
  }
}

export const disasterAreaChange = (disasterArea) => {
  return {
    type: actionTypes.DISASTER_AREA_CHANGE,
    payload: {
      disasterArea
    }
  }
}



