import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'

const styles = () => ({
  root: {},
  loader: {
    margin: '5rem',
    textAlign: 'center'
  }
})

class AppDemoContent extends Component {
  render () {
    const {
      classes,
      disasterSummary,
      isDataLoaded
    } = this.props

    return (
      <div>
        <Grid container justify='center'>
          <Grid item xs={12}>
            {
              !isDataLoaded &&
              <div className={classes.loader}>
                <CircularProgress size={80} />
              </div>
            }
            {
              isDataLoaded &&
              <pre>{JSON.stringify(disasterSummary, null, 2)}</pre>
            }
          </Grid>
        </Grid>
      </div>
    )
  }
}

export default withStyles(styles)(AppDemoContent)



